<template>
  <div>
    <div>Vue 3</div>
    <button @click="changeCount">修改值 {{ count }}</button>
  </div>
</template>

<script>
import { ref } from 'vue';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  setup() {
    const count = ref(0)
    const changeCount = () => {
      count.value++
    }
    return {
      count,
      changeCount
    }
  }
}
</script>

